import React from "react";
import ReactDOM from "react-dom";
import { PolySynth } from "tone";
import { Distortion } from "tone";
import { Freeverb } from "tone"

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { isLoaded: false };
    this.handleUp = this.handleUp.bind(this);
    this.handleDown = this.handleDown.bind(this);

    this.noteOne = 73.42;
    this.noteTwo = 110;
    this.noteThree = 146.83;
    this.revDamp = 10
    this.synth = new PolySynth().toMaster();

    this.rev = new Freeverb().toMaster();
    this.synth.connect(this.rev);
    this.rev.dampening.value = this.revDamp;

    this.synth.triggerAttack([this.noteOne, this.noteTwo, this.noteThree], 1);


    this.setState({ isLoaded: true });
  }

  handleUp() {
    this.updateSynth(10);
  }

  handleDown() {
    this.updateSynth(-10);
  }

  updateSynth(val){
    this.revDamp = this.revDamp + val/10;
    this.rev.dampening.value = this.revDamp;

    this.noteOne = this.noteOne + val;
    this.noteTwo = this.noteTwo + val;
    this.noteThree = this.noteThree + val;
    this.synth.triggerAttack([this.noteOne, this.noteTwo, this.noteThree], 1);
  }

  

  render() {
    
    return (

      
      <div>
        <div>
        <button onMouseEnter={this.handleUp}>
          up
        </button>
        <button onMouseEnter={this.handleDown}>
          down
        </button>
        </div>
        <button onMouseEnter={this.handleUp}>
          up
        </button>
        <button onMouseEnter={this.handleDown}>
          down
        </button>
        <div>
        <button onMouseEnter={this.handleUp}>
          up
        </button>
        <button onMouseEnter={this.handleDown}>
          down
        </button>
        </div>
        <button onMouseEnter={this.handleUp}>
          up
        </button>
        <button onMouseEnter={this.handleDown}>
          down
        </button>
        <div>
        <button onMouseEnter={this.handleUp}>
          up
        </button>
        <button onMouseEnter={this.handleDown}>
          down
        </button>
        </div>
        <button onMouseEnter={this.handleUp}>
          up
        </button>
        <button onMouseEnter={this.handleDown}>
          down
        </button>
        <div>
        <button onMouseEnter={this.handleUp}>
          up
        </button>
        <button onMouseEnter={this.handleDown}>
          down
        </button>
        </div>
        <button onMouseEnter={this.handleUp}>
          up
        </button>
        <button onMouseEnter={this.handleDown}>
          down
        </button>
      </div>
      
    );
  }
}

ReactDOM.render(<App />, document.getElementById("root"));